<template>
    <div id="ViewWork">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>
            <li class="collection-item">
                Worker ID#: {{workers_id}}
            </li>
            <li class="collection-item">
                Worker department: {{department}}
            </li>
            <li class="collection-item">
                Worker position: {{position}}
            </li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteWorker" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-work', params: {work_id: workers_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'ViewWork',
        data () {
            return {
                'workers_id': null,
                'name': null,
                'department': null,
                'position': null
            }
        },
        beforeRouteEnter (to, from, next) {
            db.collection('workers').where('workers_id',
            '==', to.params.work_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        next(vm => {
                            vm.workers_id = doc.data().workers_id
                            vm.name = doc.data().name
                            vm.department = doc.data().department
                            vm.position = doc.data().position
                        })
                    })
                })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                db.collection('workers').where('workers_id', '==', this.$route.params.work_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.workers_id = doc.data().workers_id
                        this.name = doc.data().name
                        this.department = doc.data().department
                        this.position = doc.data().position
                    })
                })
            },
            deleteWorker () {
                if(confirm('Are you sure?')) {
                    db.collection('workers').where('workers_id', '==', this.$route.params.work_id).get().then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        })
                    })
                }
            }
        }
    }
</script>


