<template>
    <div id="Dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Workers</h4></li>
            <li v-for="workers in workers" v-bind:key="workers.id" class="collection-item">
                <div class="chip">{{workers.department}}</div>
                {{workers.workers_id}}: {{workers.name}}
                <router-link class="secondary-content" v-bind:to="{name:'view-work', params:{work_id: workers.workers_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'Dashboard',
        data () {
            return {
                workers: []
            }
        },
        created () {
            db.collection('workers').orderBy('department').get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        'id': doc.id,
                        'workers_id': doc.data().workers_id,
                        'name': doc.data().name,
                        'department': doc.data().department,
                        'position': doc.data().position
                    }
                    this.workers.push(data)
                })
            })
        }
    }
</script>


