<template>
    <div id="NewWork">
        <h3>Add New Worker</h3>
        <div class="row">
            <form @submit.prevent="saveWorker" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="workers_id" required>
                        <label>Worker ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="department" required>
                        <label>Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                        <label>Position</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'NewWork',
        data () {
            return {
                'workers_id': null,
                'name': null,
                'department': null,
                'position': null
            }
        },
        methods: {
            saveWorker () {
                db.collection('workers').add({
                    workers_id: this.workers_id,
                    name: this.name,
                    department: this.department,
                    position: this.position
                })
                    .then(docRef => {
                        this.$router.push('/')
                    })
                    .catch(error => console.log(err))
            }
        }
    }
</script>


