<template>
    <div id="EditWork">
        <h3>Edit Worker</h3>
        <div class="row">
            <form @submit.prevent="updateWorker" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="workers_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="department" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                    </div>
                </div>
                <button type="submit" class="btn">Edit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'EditWork',
        data () {
            return {
                'workers_id': null,
                'name': null,
                'department': null,
                'position': null
            }
        },
        beforeRouteEnter (to, from, next) {
            db.collection('workers').where('workers_id',
                '==', to.params.work_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        next(vm => {
                            vm.workers_id = doc.data().workers_id
                            vm.name = doc.data().name
                            vm.department = doc.data().department
                            vm.position = doc.data().position
                        })
                    })
                })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                db.collection('workers').where('workers_id', '==', this.$route.params.work_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.workers_id = doc.data().workers_id
                        this.name = doc.data().name
                        this.department = doc.data().department
                        this.position = doc.data().position
                    })
                })
            },
            updateWorker () {
                db.collection('workers').where('workers_id', '==', this.$route.params.work_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            workers_id: this.workers_id,
                            name: this.name,
                            department: this.department,
                            position: this.position
                        })
                            .then(() => {
                            this.$router.push({name: 'view-work', params: {work_id: this.workers_id}})
                            })
                    })
                })
            }
        }
    }
</script>


